.login {
	padding: 80px 0 230px;
	color: $white;
	min-height: calc(100vh - 331px);
	@include sm {
		padding: 20px 0 100px; }
	&__head {
		margin-bottom: 50px;
		@include sm {
			margin-bottom: 20px; } }
	&__title-middle {
		margin-bottom: 35px;
		@include md {
			margin-bottom: 20px; }
		@include sm {
			margin-bottom: 10px; } }
	&__description {
		margin-top: 5px;
		@include f(24px, 600, 36px);
		@include md {
			font-size: 18px; } }
	&__wrap {
		max-width: 370px;
		@include sm {
			max-width: 100%; }
		& + .login__row {
			margin-top: 35px;
			@include md {
				margin-top: 20px; }
			@include sm {
				margin-top: 10px; } } }
	&__out {
		max-width: 790px; }
	&__row {
		margin: 0 -25px;
		@include clr;
		& + .login__wrap {
			margin-top: 35px;
			@include md {
				margin-top: 20px; }
			@include sm {
				margin-top: 10px; } } }
	&__col {
		float: left;
		width: 50%;
		padding: 0 25px;
		@include sm {
			width: 100%; }
		&:not(:first-child) {
			@include sm {
				margin-top: 10px; } } }
	&__footer {
		margin-top: 50px;
		@include md {
			margin-top: 40px; }
		@include sm {
			margin-top: 30px; }
		.btn {
			@include sm {
				width: 100%; }
			&:not(:last-child) {
				margin-right: 50px;
				@include md {
					margin-right: 25px; }
				@include sm {
					margin: 0 0 20px; } } } } }
.captcha {
	margin: 45px 0;
	@include sm {
		margin: 30px 0; }
	&__code {
		max-width: 220px;
		padding-left: 20px;
		img {
			max-width: 100%; } } }

.qr-code {
	width: 256px;
	@include md {
		width: 200px; }
	@include sm {
		width: 168px; }
	&:not(:last-child) {
		margin-bottom: 45px;
		@include sm {
			margin-bottom: 30px; } }
	img {
		max-width: 100%;
		display: block; } }

.balance {
	border: 8px solid $light-turquoise;
	border-radius: 8px;
	@include clr;
	@include md {
		border-width: 6px; }
	@include sm {
		display: flex;
		flex-wrap: wrap;
		border-width: 0; }
	&:not(:last-child) {
		margin-bottom: 50px; }
	&__wrap {
		padding: 25px 35px;
		height: 80px;
		@include f(24px,700,1.2);
		color: $white;
		overflow: hidden;
		@include clr;
		@include md {
			padding: 18px 25px;
			height: 60px;
			font-size: 18px; }
		@include sm {
			float: none;
			order: 1;
			width: 100%;
			padding: 0;
			height: auto;
			font-size: 24px; } }
	&__text {
		float: left;
		@include sm {
			float: none; } }
	&__list {
		float: right;
		@include sm {
			float: none;
			margin: 20px 0 25px; } }
	&__item {
		position: relative;
		padding: 0 15px;
		vertical-align: top;
		display: inline-block;
		@include sm {
			padding: 0;
			font-size: 30px;
			font-weight: 700;
			line-height: 1.2;
			display: block; }
		&:not(:first-child) {
			&:before {
				position: absolute;
				content: "";
				top: 0;
				left: -1px;
				bottom: 0;
				width: 3px;
				background-color: $light-turquoise;
				@include sm {
					display: none; } } }
		&:first-child {
			padding-left: 0; }
		&:last-child {
			padding-right: 0; } }
	&__btn {
		float: right;
		height: 80px;
		padding: 26px 58px 26px 66px;
		color: $white;
		font-family: $montserrat;
		@include f(24px,700,1.2);
		background-color: $light-turquoise;
		@include md {
			padding: 12px 42px 12px 50px;
			height: 60px;
			font-size: 18px; }
		@include sm {
			float: none;
			order: 2;
			padding: 21px 50px;
			height: 64px;
			font-size: 18px;
			border-radius: 8px; }
		.icon {
			font-size: 28px;
			vertical-align: middle; } } }
