.out {
	position: relative;
	overflow: hidden; }

.inner,
.inner-sm {
	position: relative;
	z-index: 1;
	margin: 0 auto;
	padding: 0 15px;
	max-width: 1200px;
	@include clr;
	@include md {
		padding: 0 38px; } }

.inner {
	@include md {
		padding: 0 38px; }
	@include sm {
		padding: 0 5px; } }

.inner-sm {
	max-width: 1025px;
	@include md {
		padding: 0 66px; }
	@include r(839) {
		padding: 0 38px; }
	@include sm {
		padding: 0 15px; } }


.wrap {
	padding: 86px 0 150px;
	@include md {
		padding: 6px 0 150px; }
	@include sm {
		padding: 6px 0 5px; } }

.row {
	margin: 0 -17px;
	@include clr;
	@include md {
		margin: 0 -12px; } }
.col {
	float: left;
	padding: 0 17px;
	@include md {
		padding: 0 12px; }
	&-33 {
		width: 33.3%; }
	&-sm-100 {
		@include sm {
			width: 100%; } } }
