.forecasts {
	position: relative;
	padding: 75px 0 70px;
	color: $white;
	@include md {
		padding: 30px 0; }
	@include sm {
		padding: 15px 0; }
	&:before {
		position: absolute;
		content: "";
		top: 50%;
		left: 50%;
		height: 210%;
		width: 300vw;
		transform: translate(-50%,-50%) rotate(30deg);
		background-color: $blue;
		@include sm {
			width: 400vw;
			height: 200%;
			transform: translate(-50%,-36%) rotate(30deg); } }
	&__title {
		margin-bottom: 7px;
		@include md {
			margin-bottom: 5px; }
		@include sm {
			font-size: 30px; } }
	&__description {
		@include f(24px,600,1.5);
		@include md {
			font-size: 18px; }
		@include sm {
			padding-right: 30px;
			font-size: 16px; } }
	&__chart {
		margin-top: 37px;
		@include md {
			margin-top: 25px; } } }
.chart {
	&__wrapper {
		margin-top: 71px;
		@include sm {
			margin-top: 22px; } } }
.graph {
	position: relative;
	margin: 0 auto;
	padding: 0 30px;
	max-width: 1920px;
	@include sm {
		padding: 0 15px; }
	img {
		max-width: 100%;
		display: block; }
	&__head {
		position: absolute;
		top: 32px;
		left: 0;
		right: 0;
		z-index: 10;
		@include sm {
			position: relative;
			top: auto;
			margin-bottom: 15px;
			width: 100%;
			display: block; } }
	&__inner-sm {
		max-width: 932px;
		@include sm {
			padding: 0; }
		&:after {
			@include sm {
				display: none; } }
		@include sm {
			display: flex;
			white-space: wrap;
			justify-content: space-between; } }
	&__col {
		text-align: right;
		vertical-align: top;
		display: inline-block;
		@include sm {
			display: block; }
		&:not(:first-child) {
			margin-left: 45px;
			@include sm {
				margin-left: 0; } } }
	&__time {
		min-height: 12px;
		@include f(12px, 500, 1);
		@include sm {
			font-size: 10px; } }
	&__text {
		margin-top: 4px;
		@include f(24px, 700, 1);
		@include sm {
			font-size: 18px; } }
	&__parcent {
		margin-top: 4px;
		@include f(12px, 400, 1);
		color: $green;
		@include sm {
			margin-top: 10px;
			font-size: 10px; }
		.icon {
			margin-top: 2px;
			font-size: 5px;
			vertical-align: top;
			@include sm {
				font-size: 4px; } } } }

// forecast
.forecast {
	position: relative;
	font-size: 18px;
	line-height: 2;
	color: $white;
	@include md {
		font-size: 14px;
		line-height: 1.6; }
	p {
		&:not(:first-child) {
			margin-top: 55px;
			@include md {
				margin-top: 48px; } } }
	&__rates {
		padding-bottom: 687px;
		@include md {
			padding-bottom: 0; } }
	&__info {
		text-align: center;
		&:not(:first-child) {
			margin-top: 160px;
			@include md {
				margin-top: 80px; }
			@include sm {
				margin-top: 30px; } } }
	&__description {
		font-size: 24px;
		font-weight: 600;
		line-height: 1.5;
		@include md {
			font-size: 18px; }
		@include sm {
			font-size: 14px; }
		&:not(:first-child) {
			margin-top: 15px;
			@include md {
				margin-top: 8px; } } } }

.cabin-bg {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-repeat: no-repeat;
	background-size: 100% auto;
	background-position: 0 190px; }

.head {
	width: 100%;
	display: table;
	@include clr;
	&__left,
	&__right {
		vertical-align: top;
		display: table-cell; }
	&__left {
		padding-right: 10px; }
	&__right {
		text-align: right;
		.btn {
			@include md {
				min-width: 131px; }
			@include sm {
				padding-left: 10px;
				padding-right: 10px;
				min-width: 90px; } } }
	&__description {
		margin-top: 5px;
		font-size: 24px;
		font-weight: 600;
		line-height: 1.5;
		@include md {
			font-size: 18px; }
		@include sm {
			font-size: 14px; } } }

.rates {
	&:not(:first-child) {
		margin-top: 44px;
		@include sm {
			margin-top: 20px; } }
	&__row {
		margin: 0 -14px;
		@include clr; }
	&__col {
		float: left;
		padding: 0 14px;
		width: 33.3%;
		&:not(:first-child) {
			@include sm {
				margin-top: 20px; } }
		@include sm {
			width: 100%; } } }
.rate {
	text-align: center;
	border: 8px solid $turquoise;
	border-radius: 8px;
	&.is-active {
		background-color: $turquoise; }
	&__body {
		padding: 48px 10px 53px;
		line-height: 1;
		@include sm {
			padding: 38px 10px; } }
	&__gap {
		font-size: 24px;
		font-weight: 400; }
	&__price {
		margin: 21px 0 10px;
		font-size: 48px;
		font-weight: 600;
		@include sm {
			font-size: 36px; } }
	&__period {
		font-size: 24px;
		font-weight: 300; }
	&__footer {
		.btn {
			margin-bottom: -8px;
			min-width: 100%;
			border-radius: 0;
			background-color: $turquoise; } } }

.star {
	font-size: 0;
	svg {
		@include md {
			width: 40px;
			height: auto; } } }
